<template>
  <!-- ces -->
  <el-dialog width="320px" v-model="show" title="" :show-close="false">
    <el-form :model="actionFrom" label-position="left">
      <el-form-item label=":">
        <el-select
          v-model="actionFrom.albums"
          remote
          multiple
          :remote-method="remoteMethod"
          filterable
          collapse-tags
          placeholder=""
          :loading="albums.loading"
        >
          <el-option
            v-for="item in albums.data"
            :key="item._id"
            :label="item.name"
            :value="item._id"
          >
          </el-option>
          <li
            @click="getMore"
            v-if="albums.data.length < albums.count"
            class="el-select-dropdown__item"
          >
            <span style="color: #67c23a"></span>
          </li>
        </el-select>
      </el-form-item>

      <el-form-item label=":" class="title">
        <!-- <div class="upload-border">
          <el-upload
            class="avatar-uploader"
            action="/"
            accept="image/jpg, image/jpeg, image/png, image/gif"
            :auto-upload="false"
            :show-file-list="false"
            :on-change="selectFile"
          >
            <img
              v-if="uploadPic"
              :src="uploadPic"
              class="avatar"
              style="border-radius: 50%"
            />
            <i v-else class="el-icon-plus avatar-uploader-icon" />
          </el-upload>
        </div> -->

        <div style="position: relative; overflow: hidden; height: 220px">
          <!-- <i class="el-icon-plus avatar-uploader-icon" /> -->
          <!-- <div
            style="
              background-color: #fefefe;
              border: 1px solid #e7eaf3;
              padding-left: 0.75rem;
              padding-right: 0.75rem;
              z-index: 999;
              width: 100%;
              pointer-events: none;
              position: absolute;
              top: 0;
            "
          >
            <span>{{
              files.length > 0
                ? "" + files.length + ")"
                : ""
            }}</span>
          </div> -->
          <el-scrollbar style="height: 220px; width: 100%">
            <el-upload
              action="/"
              id="file"
              accept="image/jpg, image/jpeg, image/png, image/gif"
              :auto-upload="false"
              multiple
              style="min-height: 60px; width: 100%"
              ref="upload"
              :show-file-list="true"
              :on-change="selectFile"
              list-type="picture"
            >
              <div
                style="
                  background-color: #fefefe;
                  border: 1px solid #e7eaf3;
                  border-radius: 0.2rem;
                  padding-left: 0.75rem;
                  padding-right: 0.75rem;
                "
              >
                <span>{{
                  actionFrom.files.length > 0
                    ? "" + actionFrom.files.length + ")"
                    : ""
                }}</span>
              </div>
            </el-upload>
          </el-scrollbar>
        </div>
      </el-form-item>
    </el-form>

    <div style="text-align: right">
      <el-button size="mini" @click="show = false"></el-button>

      <el-button
        size="mini"
        type="primary"
        @click="confirmAction"
        v-loading="actionFrom.loading"
        ></el-button
      >
    </div>
  </el-dialog>
</template>
<script>
import { ElMessage } from "element-plus";
import { urls, http } from "../http/index.js";
export default {
  name: "UploadImg",
  data() {
    return {
      actionFrom: { loading: false, files: [], albums: [] },
      show: false,
      onAlbumCreated: null,
      copper: null,
      albums: { loading: false, page: 1, pageSize: 20, data: [], count: 0 },
      currentAlbum: null,
      loading: false,
    };
  },
  mounted() {
    this.actionFrom.loading = false;
  },
  methods: {
    confirmAction() {
      if (this.actionFrom.loading) {
        return;
      }
      var result = this.actionFrom;
      // upload
      if (result.files.length == 0) {
        this.callback("");
        return;
      }
      var forms = new FormData();
      for (var i = 0; i < result.files.length; i++) {
        forms.append("file", result.files[i].raw);
      }
      forms.append("albumIds", result.albums);
      this.actionFrom.loading = true;
      http.httpPostData(
        (result) => {
          if (result.code == 0) {
            this.callback();
            if (this.onAlbumCreated) {
              this.onAlbumCreated(this.actionFrom);
            }
            ElMessage.success(result.message);
          } else {
            this.callback(result.messag);
          }
        },
        (err) => {
          this.callback(err);
        },
        urls.path.photo.create,
        forms
      );
    },
    callback(err = "") {
      this.actionFrom.loading = false;
      if (err && err != "") {
        ElMessage.warn(err);
      } else {
        this.show = false;
        this.actionFrom.files = [];
        this.actionFrom.albums = [];
      }
    },
    selectFile(file, fileList) {
      this.actionFrom.files = fileList;
    },
    showDialog(album, callback) {
      this.show = true;
      if (!this.currentAlbum && album && album._id) {
        this.currentAlbum = album;
        this.actionFrom.albums.push(this.currentAlbum._id);
      }
      this.onAlbumCreated = callback;
      if (this.albums.data.length == 0) {
        this.getAlbumListHttp(true);
      }
    },
    getMore() {
      this.albums.page = this.albums.page + 1;
      this.getAlbumListHttp(false);
    },
    getAlbumListHttp(clear, keyword = "") {
      if (clear) {
        this.albums.page = 1;
      }
      this.albums.loading = true;
      http.httpPostBody(
        (result) => {
          this.albums.loading = false;
          if (result.code == 0) {
            if (clear) {
              this.albums.data = [];
            }
            this.albums.data.push(...result.data.data);
            this.albums.count = result.data.count;
            var index = -1;

            for (var i = 0; i < this.albums.data.length; i++) {
              if (!this.albums.data[i].name || this.albums.data[i].name == "") {
                this.albums.data[i].name = "[]";
              }
              if (
                this.currentAlbum &&
                this.currentAlbum._id &&
                this.currentAlbum._id != "" &&
                this.albums.data[i]._id == this.currentAlbum._id
              ) {
                index = i;
                if (this.albums.data[i].name) {
                  this.currentAlbum.name = this.albums.data[i].name;
                }
              }
            }

            if (index >= 0) {
              this.albums.data[index] = this.currentAlbum;
            }

            // this.albums.data.splice(index, 1);
            // console.dir(this.albums.data);
            // this.albums.data.splice(index, 0, this.currentAlbum);
            // console.dir(this.albums.data);
          } else {
            ElMessage.error(result.message);
          }
        },
        (err) => {
          this.albums.loading = false;
          ElMessage.error(err);
        },
        urls.path.album.list,
        {
          page: this.albums.page,
          pageSize: this.albums.pageSize,
          keyword: keyword,
        }
      );
    },
    remoteMethod(query) {
      this.getAlbumListHttp(true, query);
    },
  },
};
</script>
<style>
/* ---dialog */
.upload-border {
  padding: 1px;
  width: 124px;
  height: 124px;
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  position: relative;
  overflow: hidden;
}

.upload-border:hover {
  border-color: #409eff;
}
.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 120px;
  height: 120px;
  line-height: 120px;
  text-align: center;
}
.avatar {
  width: 120px;
  height: 120px;
  display: block;
}
.el-tag {
  margin: 2px;
}
</style>
