<template>
  <!-- vueCropper -->
  <el-dialog width="320px" title="" v-model="dialogVisible">
    <div class="cropper-content">
      <div class="cropper" style="text-align: center">
        <VuePictureCropper
          :boxStyle="{
            width: '100%',
            height: '100%',
            backgroundColor: '#f8f8f8',
            margin: 'auto',
          }"
          :img="img"
          :options="{
            viewMode: 1,
            dragMode: 'crop',
            aspectRatio: 1 / 1,
          }"
        />
      </div>
    </div>
    <template #footer>
      <div class="dialog-footer">
        <el-button size="mini" @click="dialogVisible = false"></el-button>
        <el-button
          size="mini"
          type="primary"
          :loading="uploadLoading"
          @click="onCropResult"
          ></el-button
        >
      </div>
    </template>
  </el-dialog>
</template>

<script>
import VuePictureCropper, { cropper } from "vue-picture-cropper";
export default {
  name: "ImgCopper",
  components: {
    VuePictureCropper,
  },
  data() {
    return {
      dialogVisible: false,
      img: "",
      callback: null,
      uploadLoading: false,
    };
  },
  methods: {
    onCropResult() {
      this.uploadLoading = true;
      cropper.getBlob().then((data) => {
        const reader = new FileReader();
        reader.readAsDataURL(data);
        reader.onload = () => {
          this.dialogVisible = false;
          this.uploadLoading = false;
          const uploadPic = String(reader.result); // result back
          if (this.callback) {
            this.callback(uploadPic, data);
          }
        };
      });
    },
    show(img, callback) {
      if (!img && img.length == 0) {
        return;
      }
      this.dialogVisible = true;
      this.img = img;
      this.callback = callback;
      this.dialogVisible.dialogVisible;
    },
  },
};
</script>
<style>
.cropper-content .cropper {
  width: auto;
  height: auto;
}
</style>
