<template>
  <!-- class="card-body" -->
  <el-card
    style="position: relative"
    @click="toAlbum(item)"
    :body-style="{ padding: '0px' }"
    class="card-body"
  >
    <div>
      <!--  <img
        :onerror="errorimg"
        :src="item.cover || 'imgs/album-default.jpeg'"
        class="image"
      /> -->

      <el-image :src="item.cover || 'imgs/album-default.jpeg'" class="image">
        <template #error>
          <div>
            <el-icon><icon-picture /></el-icon>
          </div>
        </template>
      </el-image>
      <span
        v-if="item.album"
        style="
          position: absolute;
          top: 16px;
          right: 16px;
          background-color: #fefefe;
          border: 1px solid #e7eaf3;
          padding: 3px 12px;
          color: green;
          font-size: 18px;
          font-weight: bold;
          border-radius: 1rem;
        "
      >
        </span
      >
    </div>

    <div style="margin-top: 0.5rem">
      
      <span style="font-weight: bold; font-size: 18px">{{
        item.name || ""
      }}</span>

      <!--  -->
      <div style="margin-top: 0.5rem" v-if="item.albums">
        <el-tag
          v-for="(al, index) in item.albums"
          :key="index"
          @click="toAlbum(al)"
          style="margin-right: 0.1rem; margin-bottom: 0.4rem"
          size="mini"
          >al.name</el-tag
        >
      </div>
      <div>
        <span class="time">{{ item.description || "" }} </span>
      </div>

      <div class="bottom">
        <div style="margin-bottom: 0.5rem">
          {{ item.album ? "" : "" }}
          <span class="time">{{ formatDate(item.createTime) }}</span>
        </div>
        <div style="margin-bottom: 0.5rem">
           <span class="time">{{ item.order }}</span>
        </div>
        <div>
          <el-button type="primary" size="mini"></el-button>
          <el-button type="danger" size="mini"></el-button>
        </div>
      </div>
    </div>
  </el-card>
</template>

<script>
import { mapMutations } from "vuex";
import { formatDate } from "../assets/js/utils.js";
import { Picture as IconPicture } from "@element-plus/icons";

export default {
  name: "ItemImg",
  props: {
    item: {
      type: Object,
      default: { album: true, order: 1, name: "" },
    },
  },
  components: { IconPicture },
  methods: {
    ...mapMutations("app", ["CAHNGE_META_INFO"]),
    // 
    toAlbum(album) {
      this.CAHNGE_META_INFO(album.name || "[]");
      this.$router.push(
        "./albundetail?id=" + album._id + "&name=" + (album.name || "[]")
      );
    },
    formatDate(time) {
      //  hh:mm:ss
      return formatDate(time, "yyyy-MM-dd");
    },
  },
};
</script>

<style>
.card-body {
  margin-left: 0.2rem;
  margin-right: 0.2rem;
  margin-bottom: 0.2rem;
  margin-top: 0.2rem;
}
.time {
  font-size: 13px;
  color: #999;
}

.bottom {
  margin-top: 13px;
  line-height: 12px;
  justify-content: space-between;
  align-items: center;
}

.button {
  padding: 0;
  min-height: auto;
}

.image {
  width: 100%;
  min-height: 100px;
  display: block;
  object-fit: contain;
  zoom: 1.42678;
}
</style>
